@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
.list-group-item{font-family:'Quicksand', sans-serif;border:none;-webkit-transition:all 0.25s;transition:all 0.25s}.remove-item{cursor:pointer;opacity:0.7;color:maroon}.todo-completed{background-color:#e6ecf5 !important;opacity:0.7;-webkit-transform:scale(0.9);transform:scale(0.9)}.note-exit-active{-webkit-animation:todo 0.5s ease-out forwards;animation:todo 0.5s ease-out forwards}@-webkit-keyframes todo{0%{-webkit-transform:translateX(0);transform:translateX(0)}30%{-webkit-transform:translateX(25px);transform:translateX(25px)}100%{-webkit-transform:translateX(-200px);transform:translateX(-200px)}}@keyframes todo{0%{-webkit-transform:translateX(0);transform:translateX(0)}30%{-webkit-transform:translateX(25px);transform:translateX(25px)}100%{-webkit-transform:translateX(-200px);transform:translateX(-200px)}}.logo{max-width:100px}.navbar{height:100px}.home h1{margin:25px}.form-control,.list-group{margin:5px}.form{margin:10px 0}

